.menuleft {
	@include position('', 0, -250px, '','');
	background: $color-midnight-blue;
	width: 250px;
	color: lighten($color-midnight-blue, 10%);
	z-index: 1000;
	height: 100% !important;
	@include transition(all 0.5s ease-in-out);
	&.active {
		@include position('', 0, 0, '',''); }
	// List
	accordion {
		.header {
			color: $color-white;
			background: $color-con;
			@include pad(0 1rem);
			@include fz(1.2rem);
			@include lh(3.6rem);
			position: relative;
			.app-icon {
				@include position(1, .7rem, 1rem, '', '');
				overflow: hidden;
				svg {
					width: 30px;
					height: 30px; } }

			i {
				@include fz(1.5rem);
				// +mar(.5rem, right)
				width: 1.5rem;
				text-align: center;
				display: inline-block;
				@include position(1, .65rem, 1rem, '', ''); }
			em {
				@include position(1, 0, '', '', 1rem);
				cursor: pointer; }
			span {
				@include transition(all 0.5s ease-in-out);
				@include pad(3rem,left); } }
		accordion-group {
			.panel {
				position: relative;
				@include border(darken($color-midnight-blue, 3%), 1px, 1, bottom);
				&:after {
					content: '';
					display: block;
					width: 100%;
					height: 1px;
					@include position(1, '', 0, -2px, '');
					background: lighten($color-midnight-blue, 10%); }
				.panel-heading {
					background: $color-midnight-blue;
					.panel-title {
						@include lh(3rem);
						@include mar(0);
						.accordion-toggle {
							text-decoration: none;
							@include fw(400);
							&:focus {
								outline: none; }
							.title {
								@include pad(.75rem 1rem);
								@include lh(1.5rem);
								color: lighten($color-midnight-blue, 20%);
								text-decoration: none;
								@include fz(1rem);
								i {
									@include fz(1.5rem);
									@include mar(.5rem, right);
									width: 1.5rem;
									text-align: center;
									display: inline-block; }
								.label {
									@include fz(.8rem);
									@include fw(400); }
								span {
									@include transition(all 0.5s ease-in-out); } } } } }
				.panel-collapse {
					.panel-body {
						.content {
							// +pad(.5rem 0)
							.nav {
								.nav-item {
									.nav-link {
										@include pad(.5rem 1rem);
										display: block;
										@include link(lighten($color-midnight-blue, 20%), $color-white, $color-white);
										@include lh(1.5rem);
										i {
											@include fz(1.1rem);
											@include mar(.5rem, right); } }
									&:hover {
										@include bg(darken($color-midnight-blue, 3%), '', ''); } } } } } } }
			&.panel-open, &:hover {
				.panel {
					.panel-heading {
						background: darken($color-midnight-blue, 7%);
						.panel-title {
							.accordion-toggle {
								.title {
									color: $color-white; } } } }
					.panel-collapse {
						background: darken($color-midnight-blue, 5%); } } }
			&.panel-open {
				.panel {
					.panel-collapse {
						@include border(darken($color-midnight-blue, 3%), 1px, 1, top); } } } } }
	.version {
		@include justify-content(flex-end);
		@include flex-basis(0);
		@include flex-grow(1);
		@include pad(1rem);
		text-align: center;
		color: $color-con; }
	.layout {
		accordion {
			.headersub {
				color: $color-white;
				@include pad(0 1rem);
				@include fz(1.2rem);
				@include lh(3rem);
				height: 3rem !important;
				background: darken($color-midnight-blue, 10%);
				white-space: nowrap;
				overflow: hidden;
				* {
					white-space: nowrap; }
				i {
					@include position(2, '', '', '', ''); }
				span {
					@include transition(all 0.5s ease-in-out);
 } }					// +pad(1rem,left)
			accordion-group {
				.panel {
					.panel-collapse {
						.panel-body {
							.content {
								label {
									@include mar(0);
									@include link(lighten($color-midnight-blue, 20%), $color-white, $color-white);
 } } } } } } } }									// display: block
	&.active {
		.layout {
			accordion {
				.headersub {
					i {
						@include fz(1.5rem); }
					span {
						@include mar(1rem, left); } } } } } }
